/*==================== SIDEBAR ====================*/
.sidebar {
    width: 8rem;
    height: 100%;
}

.sidebar__logo {
    padding-bottom: 0.5rem;
}

.logo_img {
    font-size: 1.375rem;
    line-height: var(--line-height-normal);
    font-weight: var(--font-regular);
    letter-spacing: 1.5px;
    color: var(--text-color);
}

.logo_img .logo_letter_second {
    transition: all 0.2s ease-in;
}

.logo_img:hover .logo_letter_second {
    color: var(--hover-color);
}

.social__networks {
    margin-top: 10rem;
}

.network__box {
    margin-bottom: 0.625rem;
}

.network__box a {
    font-size: 1.75rem;
    color: var(--text-color);
}

/*=============== MEDIA QUERIES ===============*/
/* For medium devices */
@media screen and (max-width: 575px) {
    .sidebar {
        width: 4rem;
    }
}

/* For small devices */
@media screen and (max-width: 440px) {
    .sidebar {
        order: 2;
        width: 100%;
        margin-top: 15px;
    }

    .sidebar__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .social__networks {
        display: flex;
        align-items: center;
        gap: 10px;
        margin-top: 0;
    }

    .network__box {
        margin-bottom: 0;
    }
}